
$width:100%; 
/*****
RESET SHEET
**********/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, footer, header, menu, nav, section {
  display: block;
}
body {
  line-height: 1;
}
ol, ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}


/*************
DIAGNOSTIC
*****************/



img[alt=""] {}
img:not([alt]) {border: 5px solid red;}

a[href]:not([title]) {border: 5px solid red;}
a[title=""] {outline: 3px dotted red;}
a[href="#"] {background: lime;}
a[href=""] {background: fuchsia;}

/*********
 GENERAL 
********/

*, *:before, *:after { -webkit-box-sizing: border-box;
-moz-box-sizing: border-box;
box-sizing: border-box;
}


html, body{

	 font: 300 16px/22px "Lato", "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
	 position: relative; width:$width;
}

img { max-width:$width;}

main { position: relative; min-height: 100vh;  color:white;}

.hwp { height: 100vh; width:$width; position: relative;}
.text--white { color: #fff; }
.text--grey { color: #4F4F4F;}
.mgtp { margin-top: .3em;}

.firstpage {

	 padding:2em 1em 0; 
	background: url('../img/background.png') no-repeat bottom; background-size: cover;

   .logo {

   		margin-top:3em;
   		img { height:45px;}
   }


   .line { border-bottom: 2px solid #fff;  margin: 0.5em 0;}

   .message {

   	 width:60%;
   	 color: #fff;
   	 margin-top:1em; 

   	 	h5 { font-weight: bold;}

   	    p {
   	    	 font-size: 0.8rem;
   	    }
   }

}



/*** bottom of page and @margin-left removes pawdding-left of parent element and centers item**/
.nextpage{

	position: absolute;
	bottom: calc(100% - 95vh);
	display: flex;
    flex-direction: column;
    align-items: center;
    margin-left:-1em;
    width:inherit; color: #BBBFC1;

	p {   font-size: 0.8rem; }
	span { display: inline-block; }
	span:first-child { height:14px; width:14px;}
}


.aboutUs {

	background: url('../img/aboutUs-bg.png') no-repeat bottom; background-size: cover; padding:1em;

	.aboutUs__msg {
		margin-top: 60%;
	}
}

.page--header {

	span { height:20px; }

	h4 { display:inline-block; color: #929799; font-size:1.1rem;  margin-left: 0.5em; vertical-align: top; }
}

.portfolio { 

	
	background: url('../img/portfolio-bg.png') no-repeat bottom; background-size: cover; padding:1em;

	.portfolio__msg { margin-top: calc(100vh - 60vh); text-align: center; }
}



.contactUs { background-color:#E5E5E5; padding:1em; color:#4F4F4F; padding-bottom: 0;}
.fnt { font-size: 0.7em; }
.contactUs__dets { background-color:#E5E5E5; margin:0 -1em;

  > div {
   	  padding:1em;
   }

}

.contactUs__form {

	margin-right:0.8em;

	input[type="text"], input[type="email"], textarea
	{
		padding:0.7em 1em;
		width:$width;
		background: #F2F2F2;
		border:1px solid #E0E0E0;
		margin-bottom:0.6em;
		display:block;



		&:focus {

			border: 1px solid #EB5757;
		}

	}

	
	::placeholder {
		
		font-size:0.9em;
		color: #4F4F4F;
		font-style:italic;
	}

	button{

		background-color: #EB5757;
		display:inline-block;
		outline:none;
		border:none;
		padding: 0.7em 1em;
		color:#fff;
		transition:all 0.3s ease-in;
		margin-left:65%;

		span { display:inline-block; width:14px; 

				img {
					
				}
		}

		&:hover{

			background-color: rgba(235, 87, 87, 0.44);
			border: 2px solid #EB5757;
		}
	}
}

.contactUs__details { background-color:#4F4F4F; color:#fff;

	h5 {

		font-size: 0.8em;
		font-weight: bold;
		letter-spacing: 0.1em;
	}


	.fnt {
		span {
			text-decoration: underline;
		}
	}

}


.contact__loc {

	margin-bottom:0.5em;

	span {

		display:inline-block;
		width: 14px;
	}

	p { font-size: 0.9em; }
}



.map {

	margin:0 -1em;
	height:50vh;
}
 
footer { background-color:#333333;  display:table; width:100%; padding:0.5em 0;

	> div {

		display:table-cell;
		color:#6D6D6D;
		font-size:0.7em;
		padding:0 0.5em;
	}


	.copyright {
		border-right: 2px solid white;
		vertical-align:middle;
		width:50%;
	}

	

	.instagram {

		span { display:inline-block; 

			img { width:20px;}
		}

		div { display: inline-block; margin-left: 0.5em; width: 70%; line-height: 1.2;  }
	}


 }


